.footer {
  padding-top: 90px;
  padding-bottom: 72px;
  background-color: $black;
  color: $white;

  @media (max-width: $tabletMax) {
    padding-top: 50px;
    padding-bottom: 32px;
  }

  @media (max-width: $mobileMax) {
    padding-bottom: 10px;
  }
}

.footer__wrapper {
  display: grid;
  grid-template-columns:
    minmax(350px, 588px) minmax(185px, 244px) minmax(145px, 223px)
    auto;

  @media (max-width: $tabletMax) {
    padding-right: 150px;
    grid-template-columns: minmax(255px, 350px) minmax(203px, 300px) min-content;
    grid-template-rows: auto;
  }

  @media (max-width: $mobileMax) {
    padding-right: 15px;
    grid-template-columns: 1fr;
    grid-template-rows: auto auto auto;
  }
}

.footer__sign-up {
  padding-right: 50px;

  @media (max-width: $tabletMax) {
    margin-bottom: 45px;
    padding-right: 10px;
    grid-column: 1 / -1;
  }

  @media (max-width: $mobileMax) {
    padding-right: 0;
  }
}

.footer__sign-up h3 {
  margin: 0;
  font-family: $cormorantGaramond;
  font-style: normal;
  font-weight: 700;
  font-size: 34px;
  line-height: 44px;
  letter-spacing: 0.03em;

  @media (max-width: $mobileMax) {
    padding-right: 64px;
  }
}

.footer__sign-up p {
  margin-top: 7px;
  margin-bottom: 40px;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: $grey1;

  @media (max-width: $tabletMax) {
    margin-top: 10px;
    margin-bottom: 43px;
  }

  @media (max-width: $mobileMax) {
    padding-right: 64px;
  }
}

.footer__sign-up-form {
  padding-bottom: 10px;
  display: grid;
  grid-template-columns: 1fr auto;
  max-width: 500px;
  border-bottom: 1px solid $white;

  @media (max-width: $tabletMax) {
    padding-bottom: 5px;
    max-width: 650px;
  }
}

.footer__sign-up-field input {
  font-family: $poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  width: 95%;
  color: $white;
  background-color: transparent;
  border: none;
  outline: none;

  ::placeholder {
    color: $grey1;
  }
}

.footer__signup-button {
  padding: 0;
  font-family: $poppins;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  color: $white;
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;

  &:hover,
  &:focus {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.3;
  }
}

.footer__signup-button span {
  margin-right: 10px;
}

.footer__info-list {
  @media (max-width: $mobileMax) {
    margin-bottom: 18px;
  }
}

.footer__info-item {
  margin-bottom: 18px;
}

.footer__info-item a {
  width: fit-content;
  color: $white;
}

.footer__info-list--socials {
  @media (max-width: $mobileMax) {
    padding-top: 5px;
    display: flex;
    flex-wrap: wrap;
  }
}

.footer__info-list--socials .footer__info-item {
  @media (max-width: $mobileMax) {
    margin-right: 25px;
    margin-bottom: 2px;
  }
}

.footer__info-list--socials a {
  display: flex;
  align-items: center;
}

.footer__info-list--socials a svg {
  margin-right: 15px;

  @media (max-width: $mobileMax) {
    margin-right: 0;
    width: 21px;
    height: 21px;
  }
}

.footer__info-list--socials a span {
  @media (max-width: $mobileMax) {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip: rect(0 0 0 0);
    clip-path: inset(100%);
    overflow: hidden;
  }
}

.footer__info-link--facebook {
  &:hover,
  &:focus {
    color: $facebookBlue;
  }

  &:active {
    opacity: 0.5;
  }
}

.footer__info-link--instagram {
  &:hover,
  &:focus {
    color: $instagramPurple;
  }

  &:active {
    opacity: 0.5;
  }
}

.footer__info-link--pinterest {
  &:hover,
  &:focus {
    color: $pinterestRed;
  }

  &:active {
    opacity: 0.5;
  }
}

.footer__info-link--twitter {
  &:hover,
  &:focus {
    color: $twitterBlue;
  }

  &:active {
    opacity: 0.5;
  }
}
